.intro {
  font-weight: 700;

  &__greeting,
  &__name,
  &__job {
    @include breakpoint(md) {
      display: inline-block;
      
      border-right: 1px solid #fff;
      overflow: hidden;
      white-space: nowrap;
      letter-spacing: .15em; /* Adjust as needed */
     }
  }

  &__greeting {
    margin-bottom: 0;
    font-size: 3rem;

    &--lang-en {
      @include breakpoint(md) {
        animation: 
        typing 3.5s steps(35, end),
        blink-caret .75s step-end forwards;
      }
    }
  }

  &__name {
    margin: 1rem 0 0;

    font-size: 2.5rem;

    @include breakpoint(md) {
      animation: 
      typing 2.5s steps(25, end) backwards,
      blink-caret .75s step-end 1 forwards 1.1s; 
    }

    &--lang-en {
      @include breakpoint(md) {
        border-color: transparent;

        animation: 
          typing 3.5s steps(35, end) 1s backwards,
          blink-caret .75s step-end 1 forwards 1.1s;
      }
    }
  }

  &__job {
    margin-top: .5rem;
    font-size: 2rem;

     @include breakpoint(lg) {
       font-size: 2.5rem;
     }
     
    @include breakpoint(md) {
      border-color: transparent;

      animation: 
        typing 3.5s steps(35, end) 2.5s backwards, 
        blink-caret .75s step-end infinite forwards 1.5s;
      }
     

    &--accent {
      color: map-get($colors, accent);
    }
  }
}

@keyframes typing {
  from { max-width: 0 }
  to { max-width: 100% }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: #fff; }
}